<template>
  <div class="my">
    <loading v-show="isLoading" />
    <div v-if="isLogin">
      <div class="bg-box" :style="{backgroundImage: 'url('+userData.userBg+')'}">
        <div class="change-img">更换背景</div>
        <div class="updata-img">
          <van-uploader :max-size="maxSize" :after-read="afterRead" @oversize="overSize" />
        </div>
        <div class="up clearFix">
          <div class="img-box fl">
            <img class="auto-img" :src="userData.userImg" alt />
          </div>
          <div class="name fl">{{userData.nickName}}</div>
        </div>
        <div class="down">{{userData.desc == ""? "此用户很懒,啥也没留下" : userData.desc}}</div>
      </div>
      <div class="v-cell">
        <van-cell
          class="v-cell-box"
          :title="item.text"
          is-link
          v-for="(item,index) in textData"
          :key="index"
          @click="togglePage(index)"
        />
      </div>
    </div>
    <div v-else>
      <div class="tip">
        <van-empty class="v-tip" description="未登录啥都没有哦" />
        <span @click="goLogin">点我登录</span>
      </div>
    </div>
  </div>
</template>

<script>
import loading from "../../components/loading";
import "../../assets/css/my.less";
export default {
  components: { loading },
  data() {
    return {
      isLoading: false,
      textData: [
        {
          text: "账号管理",
          name: "IdMsg"
        },
        {
          text: "安全中心",
          name: "Security"
        }
      ],
      isLogin: false,
      userData: {},
      // 限制上传图片大小
      maxSize: 1024 * 1024
    };
  },
  created() {
    if (localStorage.getItem("__Tk")) {
      this.isLogin = true;
      this.getUserData();
    }
  },
  methods: {
    // 上传背景图片
    afterRead(file) {
      this.isLoading = true;
      // 此时可以自行将文件上传至服务器
      // 获取base64格式图片
      let imgData = file.content.split(",")[1];
      let type = file.file.type.split("/")[1];
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          imgType: type,
          serverBase64Img: imgData
        }
      })
        .then(result => {
          if (result.data.code == "I001") {
            this.userData.userBg = result.data.userBg;
          } else {
            this.$toast(result.data.msg);
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    // 跳转到对应页面
    togglePage(index) {
      this.$router.push({ name: this.textData[index].name });
    },
    // 跳转到登录页面
    goLogin() {
      this.$router.push({ name: "Login" });
    },
    // 获取用户数据
    getUserData() {
      this.isLoading = true;
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "get",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          if (result.data.code == "A001") {
            this.userData = result.data.result[0];
            this.isLoading = false;
          }
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    // 上传图片超出大小提示
    overSize() {
      this.$toast("图片大小不能超过1M哦");
    }
  }
};
</script>
